import styled from "styled-components";

const StyledDay = styled.div`

  width: 150px;
  flex-wrap: no-wrap;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  text-align: center;
  position: relative;
  border: 1px solid #fff;
  border-radius: 5px;
  height: 160px;
  padding: 10px;
  background-color: rgba(10, 10, 48, 0.81);
  margin-bottom: 20px;

  @media(max-width: 468px){

      width: 100%;
		
`;

const StyledDescription = styled.p`
  font-weight: 300;
`;

const StyledImg = styled.img`
  margin: 0 auto;
  width: 50%;
  max-width: 80px;
`;

const StyledTemp = styled.p`
  font-size: 1.4rem;
`;

export { StyledDay, StyledDescription, StyledImg, StyledTemp };
