import React from "react";
import StyledHeadline from "./styled";

const CityName = props => {
  if (props.city) {
    console.log(props.city.city.name);
    return (
      <>
        <StyledHeadline>
          {props.city.city.name}, {props.city.city.country}:
        </StyledHeadline>
      </>
    );
  } else {
    return <></>;
  }
};

export default CityName;
