import React, { useState } from "react";
import CityName from "../../components/CityName/index";
import Day from "../../components/Day/index";
import CityInfo from "../../components/CityInfo/styled";

const HomePage = () => {
  const [input, setInput] = useState("");
  // const [cityState, setCityState] = useState("");
  const [promiseResponse, setPromiseResponse] = useState("");
  const [showDays, setShowDays] = useState(false);
  const handlePromise = url => {
    return new Promise(function(resolve, reject) {
      const xhr = new XMLHttpRequest();
      xhr.timeout = 30000;
      xhr.onreadystatechange = function(e) {
        //on every state change 0 1 2 3 4
        if (xhr.readyState === 4) {
          //done
          if (xhr.status === 200) {
            //ok
            resolve(xhr.response);
          } else {
            reject(xhr.status);
          }
        }
      };
      xhr.ontimeout = function() {
        reject("timeout");
      };
      xhr.open("get", url, true);
      // xhr.setRequestHeader("Accept", "application/json");
      xhr.send();
    });
  };

  const handleChange = e => {
    setInput(e.target.value);
  };

  const keyPress = e => {
    if (e.keyCode === 13) {
      handleStart();
    }
  };
  const handleStart = () => {
    const myPromise = handlePromise(
      "https://api.openweathermap.org/data/2.5/forecast?q=" +
        input +
        "&APPID=b282efd98757cf659022d7f9444137be"
    );

    function parseResponse(response) {
      const promiseResponse = JSON.parse(response);
      setPromiseResponse(promiseResponse);
      //   console.log(cityState);
      setShowDays(true);
    }

    myPromise.then(parseResponse).catch(function(error) {
      console.error("Something went wrong ... ", error);
    });
  };

  return (
    <div className="wrapper">
      <div className="heading">
        <label id="header">
          Check current<span className="important"> weather </span>for:
          <input
            type="text"
            id="cityinput"
            placeholder="city"
            onChange={handleChange}
            onKeyDown={keyPress}
          />
        </label>
      </div>
      <CityName city={promiseResponse} />
      <div>
        {showDays ? (
          <CityInfo>
            <Day response={promiseResponse.list[0]} />
            <Day response={promiseResponse.list[1]} />
            <Day response={promiseResponse.list[2]} />
            <Day response={promiseResponse.list[3]} />
            <Day response={promiseResponse.list[4]} />
            <Day response={promiseResponse.list[5]} />
            <Day response={promiseResponse.list[6]} />
            <Day response={promiseResponse.list[7]} />
          </CityInfo>
        ) : null}
      </div>
    </div>
  );
};

export default HomePage;
