import { createGlobalStyle } from "styled-components";
import img from "../images/background3.jpg";

const GlobalStyle = createGlobalStyle`

html{box-sizing:border-box;}
*,
*::before,
*::after {
	box-sizing: inherit;
	margin: 0;
	padding: 0;
}
@import url('https://fonts.googleapis.com/css?family=Roboto:400,700');
@import url('https://fonts.googleapis.com/css?family=Sarabun:400,700');
html {
	font-size: 10px;
  }
  
  body {
	font-family: "Montserrat", sans-serif; /*fw= 300, 500, 800  */
	background-image:url(${img});
	background-size: cover;
	background-position: center;
	color: white;
	font-weight: 500;
	height: 100vh;
  }
  
  #header {
	font-size: 3rem;
	font-weight: 500;
	line-height: 50px;
	}
  
  .wrapper {
	max-width: 1400px;
	padding: 30px 0;
	height: 100%;
	min-height: 100vh;
	margin: auto;
	text-align: center;
	}
	
	@media(min-width: 768px){
		.wrapper {
			padding: 30px;
		}
	}
  
  p {
	font-weight: bold;
  }
  
  #cityinfo {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
  }
  
  #cityinput {
	width: 250px;
	font-family: "Montserrat", sans-serif;
	border: 1px solid #fff;
	border-radius: 3px;
	font-size: 3rem;
	margin-left: 20px;
		
	@media(min-width: 768px){
		#cityinput {
			margin-left: 0;
		}
	}
  }

  .important {
	letter-spacing: 4px;
	font-weight: 300;
	font-size: 1.5em;
  }
  
  .heading {
	display: flex;
	align-items: baseline;
	justify-content: center;
  }
  
  .title {
	font-size: 4rem;
	text-align: center;
	font-weight: 300;
  }
  
  .error {
	color: red;
	font-size: 2rem;
	font-weight: 300;
	text-align: center;
  }
	
`;

export default GlobalStyle;
