import React from "react";
import { StyledDay, StyledDescription, StyledImg, StyledTemp } from "./styled";

const Day = props => {
  if (props.response) {
    return (
      <StyledDay>
        <p>{props.response.dt_txt.substring(11, 16)}</p>
        <StyledImg
          src={
            "http://openweathermap.org/img/w/" +
            props.response.weather[0].icon +
            ".png"
          }
          alt=""
        />
        <StyledDescription>
          {props.response.weather[0].description}
        </StyledDescription>
        <StyledTemp>
          {parseInt(props.response.main.temp - 273) + "'\u00b0C"}
        </StyledTemp>
      </StyledDay>
    );
  } else {
    return <></>;
  }
};

export default Day;
